.landingnavbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #ffffff;
    color: #1a1a1a; 
    font-family: 'Arial', sans-serif;
    position: sticky;
    top: 0;
    z-index: 1000;
  }

  .landingnavbar-brand {
    font-size: 1.5rem;
    font-weight: bold;
    text-decoration: none;
    color: #fff;
  }

  .menu-btn {
    display: none; 
    font-size: 1.5rem;
    cursor: pointer;
  }
  
  .realestateblack {
    margin-left: 615px;
    margin-top: 10px;  
  }

  body {
    background: #00BFFF;
    background: linear-gradient(90deg, #00BFFF(9, 13, 18) 13%, #00BFFF(17, 22, 28) 58%);
    margin: 0;
    font-family: Arial, sans-serif;
  }

  h1 {
    color: #ffffff;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }


.landing-page {
  text-align: center;
  margin-top: 50px;  /* Space between navigation bar and landing page */
  max-width: 800px; /* Set the max width of the container */
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  border: 2px solid #ccc; /* Optional: Add a border for better visibility */
  border-radius: 8px; /* Optional: rounded corners */
  background-color: #f9f9f9; /* Optional: background color */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Optional: subtle shadow for aesthetics */
  margin-bottom: 40px; /* Optional: space below the landing page */
}

.slideshow {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 100%; /* Make slideshow take full width of the container */
  height: 300px; /* Set height for the slideshow */
}

.arrow {
  background: transparent;
  border: none;
  font-size: 2rem;
  cursor: pointer;
  padding: 1rem;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.arrow.left {
  left: 10px;
}

.arrow.right {
  right: 10px;
}

.slide-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 20px;
}

h2 {
  margin-bottom: 20px;
}
